<template>
  <div class="visualization" id="visualization">
    <select name="" id="selectButton"></select>
    <br />
    <button class="button-1" id="button-1">Tasa</button>
    <button class="button-2" id="button-2">Total</button>
    <br />

    <svg class="svg-visualization" id="svg_visualization"></svg>
  </div>
</template>

<script>
import * as d3 from "d3";

var data1;
var data2;

export default {
  name: "Visualization",
  data() {
    return {};
  },
  methods: {},
  mounted() {
    /*
     */
    // Obtenemos los datos
    // var csvUrl = "https://raw.githubusercontent.com/holtzy/data_to_viz/master/Example_dataset/3_TwoNumOrdered_comma.csv";
    // var csvUrl = "data3.csv";

    // //Read the data
    Promise.all([d3.csv("/data3.csv"), d3.csv("/data4.csv")])
      .then(([d1, d2]) => {
        data1 = d1;
        data2 = d2;
        console.log(data1);
        console.log(data2);

        // var data1 = [
        //     {ser1: 0.3, ser2: 4},
        //     {ser1: 2, ser2: 16},
        //     {ser1: 3, ser2: 8}
        // ];

        // var data2 = [
        //     {ser1: 1, ser2: 7},
        //     {ser1: 4, ser2: 1},
        //     {ser1: 6, ser2: 8}
        // ];

        // set the dimensions and margins of the graph
        var margin = { top: 10, right: 30, bottom: 50, left: 60 },
          width = 460 - margin.left - margin.right,
          height = 400 - margin.top - margin.bottom;
        // append the svg object to the body of the page
        var svg = d3
          .select("#svg_visualization")
          .attr("width", width + margin.left + margin.right)
          .attr("height", height + margin.top + margin.bottom)
          .append("g")
          .attr(
            "transform",
            "translate(" + margin.left + "," + margin.top + ")"
          );

        // Initialise a X axis --> it is a date format
        var x = d3.scaleTime().range([0, width]);
        // var xAxis = d3.axisBottom().scale(x).tickFormat(d3.timeFormat("%Y-%m-%d"));
        var xAxis = d3.axisBottom().scale(x).tickFormat(d3.timeFormat("%Y-%m"));
        svg
          .append("g")
          .attr("transform", "translate(0," + height + ")")
          .attr("class", "myXaxis");
        // Initialise a Y axis
        var y = d3.scaleLinear().range([height, 0]);
        var yAxis = d3.axisLeft().scale(y);
        svg.append("g").attr("class", "myYaxis");

        // Compilamos un parser para las fechas (por ejemplo: 2-May-12)
        var parseTime = d3.timeParse("%Y-%m-%d");

        // Creamos una línea que más tarde cargaremos en el path
        var valueLine = d3
          .line()
          .x(function (d) {
            return x(d.date);
          })
          .y(function (d) {
            return y(d.value);
          });

        // Los formateamos
        data1.forEach(function (d) {
          // Parse a dia por mes
          d.date = parseTime(d.date);
          // Parse a valor numérico
          d.value = +d.value;
        });
        data2.forEach(function (d) {
          // Parse a dia por mes
          d.date = parseTime(d.date);
          // Parse a valor numérico
          d.value = +d.value;
        });

        function update(data) {
          /* Takes a dataset as input and update the plot
           */
          // Create the X axis
          x.domain(
            d3.extent(data, function (d) {
              return d.date;
            })
          );
          svg
            .selectAll(".myXaxis")
            .transition()
            .duration(3000)
            .call(xAxis)
            .selectAll("text")
            .style("text-anchor", "end")
            .attr("dx", "-.8em")
            .attr("dy", ".15em")
            .attr("transform", function () {
              return "rotate(-40)";
            });
          // Create the Y axis
          y.domain([
            0,
            d3.max(data, function (d) {
              return d.value;
            }),
          ]);
          svg.selectAll(".myYaxis").transition().duration(3000).call(yAxis);

          // Create a update selection: bind to the new data
          var u = svg.selectAll(".lineTest").data([data], function (d) {
            return d.date;
          });
          // Update the line
          u.enter()
            .append("path")
            .attr("class", "lineTest")
            .merge(u)
            .transition()
            .duration(3000)
            .attr("d", valueLine)
            .attr("fill", "none")
            // .attr("stroke", function(d){ return color(d.key) })
            .attr("stroke", "steelblue")
            .attr("stroke-width", 1.5);
        } // end update

        // At the beginning, I run the update function on the first dataset
        update(data1);

        d3.select("#button-1").on("click", function () {
          update(data1);
        });
        d3.select("#button-2").on("click", function () {
          update(data2);
        });
      })
      .catch((err) => console.log(err));
  }, // end mounted
};
</script>

